*{
    font-family:JioBold;
}

.container{
    width: 100%;
    height: 100%;
    display:grid;
    grid-template-columns: repeat(5,20%);
    grid-gap:5px;
}

.card{
    background-color: white;
    border-radius: 5px;
    padding: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

p{
    font-size: 12px;
    font-family: JioBold;
    text-overflow:wrap;
    overflow:wrap;
}
.price{
    font-size: 12px;
    display:flex;
    justify-content:left;    
    text-overflow:wrap;
    gap:5px;
}

h4{
    text-decoration: line-through;
}
h1{
    
    border: 1px solid rgb(114, 111, 111);
    height: 100%;
    border-radius: 10px;
}

.topdeal{
    text-align: left;
    font-size:1em;
}